body * {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
}

.app {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 50px;
}

header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
  margin-right: 10px;
  width: 100%;
}

header .title {
  font-family: 'Pacifico', cursive;
  font-size: 40px;
}

header .header-menu {
  margin-right: 8px;
}

.button {
  background: teal;
  border: none;
  border-radius: 4px;
  box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
  color: white;
  padding: 8px;
}

.button:hover {
  opacity: .75;
  cursor: pointer;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1050px;
  width: 100%;
}

.card {
  color: white;
  cursor: pointer;
  display: flex;
  height: 200px;
  margin: 0 10px 10px 0;
  width: 200px;
}

.modal {
  align-items: center;
  background-color: white;
  box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 10px;
  max-width: 600px;
  min-width: 300px;
  width: 100%;
}

.modal-close-wrapper {
  display: flex;
  justify-content: right;
  width: 100%;
}

.modal-close {
  background-color: transparent;
  border: none;
  color: teal;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  margin: 10px;
  outline: none;
}

.modal-header {
  display: flex;
  font-size: 20px;
  font-weight: bold;
  width: 100%;
}

.modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 225px;
  padding: 50px;
  width: 100%;
}

.ordered-list {
  display: block;
}

.ordered-list li {
  display: list-item;
  margin-bottom: 16px;
}

#cursorText {
  position: absolute;
  background-color: white;
  border: 1px dashed teal;
  padding: 4px;
}

#cursorText:empty {
  border: none;
  padding: 0;
}

footer {
  margin: 50px;
}

footer a {
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
